import React, { Component } from "react";

import {
  Form,
  Icon,
  Input,
  Button,
  Checkbox,
  Dropdown,
  Menu,
  Table,
  Modal
} from "antd";

import { BrowserRouter, Link, Route, Redirect, Switch ,withRouter  } from "react-router-dom";
import userIcon from "../image/user.png";
import ListTemplate from "./ListTemplate";
import ListTemplateCss from "./ListTemplate.css";
import axios from "../model/axios";
export class ReturnWork extends Component {
  constructor(props) {
    super(props);
    this.state = {
      clickType: "list",
      qymc: "",
      data: [],
      arr:[],
    };
  }
  
  deleteComp = id => {
    const { confirm } = Modal;
    let that=this;
    confirm({
      title: '确定要删除么?',
      okText: '是',
      okType: 'danger',
      cancelText: '否',
      onOk(){
        axios
      .post("/returnApi/user/deleteCompany", {
        id: id
      })
      .then(response => {
        alert("删除成功");
        that.serachData();
      })
      .catch(function (error) {
        console.log(error);
      });
      },
      onCancel() {
        console.log('Cancel');
      },
    });
    
  };
  changeClickType = clickType => {
    this.setState((prevState, props) => ({
      clickType: clickType
    }));
  };
  componentDidMount() {
    this.serachData();
  }

  serachData() {
    axios
      .post("/returnApi/user/searchCompany", {

        id: localStorage.getItem("comp"),
        companyName: this.state.qymc

      })
      .then(response => {
        this.setState((prevState, props) => ({
          data: response
        }));
        //console.log(response);
      })
      .catch(function (error) {
        console.log(error);
      });
  }
  handleQYMC = event => {
    if (event && event.target) {
      let value = event.target.value;
      this.setState({ qymc: value }, () => {
        
        this.serachData();
      });
    }
  };

  render() {
    const menu = (
      <Menu>
        <Menu.Item>
          <a
            target="_blank"
            rel="noopener noreferrer"
            href="/returnApi/exportTemplateAll"
          >
          全部下载
          </a>
        </Menu.Item>
        <Menu.Item>
          <a
            target="_blank"
            rel="noopener noreferrer"
            href="/returnApi/exportTemplate?id=0"
          >
           企业复工复业申请表
          </a>
        </Menu.Item>
        <Menu.Item>
          <a
            target="_blank"
            rel="noopener noreferrer"
            href="/returnApi/exportTemplate?id=1"
          >
           复工复业人员统计表
          </a>
        </Menu.Item>
        <Menu.Item>
          <a
            target="_blank"
            rel="noopener noreferrer"
            href="/returnApi/exportTemplate?id=2"
          >
           企业疫情防控承诺书
          </a>
        </Menu.Item>
        <Menu.Item>
          <a
            target="_blank"
            rel="noopener noreferrer"
            href="/returnApi/exportTemplate?id=3"
          >
           复工复业疫情防控实施方案 <br />（参考提纲）
          </a>
        </Menu.Item>
      </Menu>
    );
    const rowSelection = {
      onChange: (selectedRowKeys, selectedRows) => {
        console.log(
          `selectedRowKeys: ${selectedRowKeys}`,
          "selectedRows: ",
          selectedRows
        );
        let arr=selectedRowKeys.map(v=>{return v}).join(",")
        console.log(arr)
        this.setState({ arr });
      }
    };
    
    const columns = [
      {
        title: "企业名称",
        dataIndex: "companyName",
        align: "center"
        // render: text => <a>{text}</a>,
      },
      {
        title: "统一会信用代码",
        dataIndex: "companyCode",
        align: "center"
      },
      {
        title: "经营地址",
        dataIndex: "address",
        align: "center"
      },
      {
        title: "操作",
        render: text => (
          <span>
           <Link to 
            ={{
              pathname: `/ReturnWork/intoComp/${text.id}`,
              
            }}
          >  进入企业&nbsp;&nbsp;{" "}</Link>
            <Link to 
            ={{
              pathname: `/ReturnWork/updateComp/${text.id}`,
              
            }}
          >  编辑</Link>
            &nbsp;&nbsp; <a onClick={() => this.deleteComp(text.id)}>删除</a>
          </span>
        ),
        align: "center"
      }
    ];
    return (
      <div className="ListTemplate">
        <div className="ListTemplateTitle">
          <div className="ListTemplateTitleIcon"></div>
          <div className="ListTemplateTitleFont">名单汇总</div>
        </div>
        <div className="mainTable">
          <div className="mainInput">
            <Input
              placeholder="请输入企业名称"
              style={{
                "margin-left": "3%",
                width: "200px",
                "margin-top": "10px"
              }}
              onChange={event => this.handleQYMC(event)}
            />
              <a
            target="_blank"
            rel="noopener noreferrer"
            href={"/returnApi/exportEmployeInfoAll?ids="+this.state.arr+""}
          >
            <Button
              type="primary"
              style={{
                "margin-right": "3%",
                "margin-top": "10px",
                float: "right"
              }}
            >
              人员名单批量下载
            </Button>
            </a>

            {/* <Dropdown overlay={this.menu} placement="模板下载"  style={{
               "margin-right": "1%",
               "margin-top": "10px",
               float: "right"
              }}>
              <Button>模板下载<Icon type="down" /></Button>
            </Dropdown> */}
            <Dropdown overlay={menu}>
              <Button
                type="primary"
                style={{
                  "margin-right": "1.5%",
                  "margin-top": "10px",
                  float: "right"
                }}
              >
                模板下载 <Icon type="down" />
              </Button>
            </Dropdown>
            <Link to 
            ={{
              pathname: `/ReturnWork/addComp`,
              
            }}
          >
            <Button
              type="primary"
             
              style={{
                "margin-right": "1.5%",
                "margin-top": "10px",
                float: "right"
              }}
            >
              新增
            </Button>
            </Link>
          </div>
          <div className="tableInfo">
            <Table
              rowSelection={rowSelection}
              columns={columns}
              dataSource={this.state.data}
              scroll={{ y: "500px" }}
              rowKey="id"
            />
          </div>
        </div>
        <div className="bottom">
          版权所有：苏州中科天启遥感科技有限公司 苏ICP备13020668号-1
        </div>
      </div>
    );
  }
}

export default ReturnWork;
