import React, { Component } from "react";
import axios from "../model/axios";
import {
  Form,
  Icon,
  Input,
  Button,
  Checkbox,
  Dropdown,
  Menu,
  Table
} from "antd";
import userIcon from "../image/user.png";
import statisticsCompCss from "./statisticsComp.css";
const { Column, ColumnGroup } = Table;
export class statisticsComp extends Component {
  constructor(props) {
    super(props);
    this.state = {
      clickType: "list",
      qymc: "",
      data: [
       
      ]
    };
  }
  componentDidMount() {
    this.serachData();
  }
  serachData() {
    axios
      .get("/returnApi/stats/getStatsData", {
        params:{
        companyName:this.state.qymc
        }
      })
      .then(response => {
        //console.log(response);
        this.setState((prevState, props) => ({
        data:response.data
        }));
      })
      .catch(function(error) {
        console.log(error);
      });
  }
  changeClickType = clickType => {
    this.setState((prevState, props) => ({
      clickType: clickType
    }));
  };
 
  handleQYMC = event => {
    if (event && event.target ) {
      let value = event.target.value;
      this.setState({ qymc: value }, () => {
        //这里打印的是最新的state值
       this.serachData();
      });
    }
  };

  render() {
    const menu = (
      <Menu>
        <Menu.Item>
          <a
            target="_blank"
            rel="noopener noreferrer"
            href="http://www.alipay.com/"
          >
            1st menu item
          </a>
        </Menu.Item>
        <Menu.Item>
          <a
            target="_blank"
            rel="noopener noreferrer"
            href="http://www.taobao.com/"
          >
            2nd menu item
          </a>
        </Menu.Item>
        <Menu.Item>
          <a
            target="_blank"
            rel="noopener noreferrer"
            href="http://www.tmall.com/"
          >
            3rd menu item
          </a>
        </Menu.Item>
      </Menu>
    );
    const rowSelection = {
      onChange: (selectedRowKeys, selectedRows) => {
        console.log(
          `selectedRowKeys: ${selectedRowKeys}`,
          "selectedRows: ",
          selectedRows
        );
      }
    };

    return (
      <div className="ListTemplate">
        <div className="ListTemplateTitle">
          <div className="ListTemplateTitleIcon"></div>
          <div className="ListTemplateTitleFont">汇总统计</div>
        </div>
        <div className="mainTable" style={{ height: "680px" }}>
      
          <div className="mainInput">
            <Input
              placeholder="请输入企业名称"
              style={{
                "margin-left": "3%",
                width: "200px",
                "margin-top": "10px"
              }}
              onChange={event => this.handleQYMC(event)}
            />
          

            {/* <Dropdown overlay={this.menu} placement="模板下载"  style={{
               "margin-right": "1%",
               "margin-top": "10px",
               float: "right"
              }}>
              <Button>模板下载<Icon type="down" /></Button>
            </Dropdown> */}
            <a href="/returnApi/stats/downloadStatsData">
              <Button
                type="primary"
                style={{
                  "margin-right": "1.5%",
                  "margin-top": "10px",
                  float: "right"
                }}
                // onClick={() => this.daochu()}
              >
                导出
              </Button>
            </a>
          </div>
          <div className="tableInfo">
            <Table
              dataSource={this.state.data}
              scroll={{ y: "500px", x: "3000px" }}
              rowKey="id"
              bordered
            >
              <Column title="序号" dataIndex="id" key="id" width="80px" fixed />
              <Column
                title="企业名称"
                dataIndex="companyName"
                key="companyName"
                width="180px"
                fixed
              />
              <Column
                title="申请复工人数"
                dataIndex="total"
                key="total"
                width="100px"
              />
              

              <ColumnGroup title="尚未回来人员">
                <Column
                  title="湖北"
                  dataIndex="hbNotBackCount"
                  key="hbNotBackCount"
                  width="100px"
                />
                <Column
                  title="浙江"
                  dataIndex="zjNotBackCount"
                  key="zjNotBackCount"
                  width="100px"
                />
                <Column
                  title="河南"
                  dataIndex="hnNotBackCount"
                  key="hnNotBackCount"
                  width="100px"
                />
                <Column
                  title="安徽"
                  dataIndex="ahNotBackCount"
                  key="ahNotBackCount"
                  width="100px"
                />
                <Column
                  title="江西"
                  dataIndex="jxNotBackCount"
                  key="jxNotBackCount"
                  width="100px"
                />
                <Column
                  title="广东"
                  dataIndex="gdNotBackCount"
                  key="gdNotBackCount"
                  width="100px"
                />
                <Column
                  title="苏州"
                  dataIndex="szNotBackCount"
                  key="szNotBackCount"
                  width="100px"
                />
                <Column
                  title="江苏"
                  dataIndex="jsNotBackCount"
                  key="jsNotBackCount"
                  width="100px"
                />
                <Column
                  title="其他"
                  dataIndex="otherNotBackCount"
                  key="otherNotBackCount"
                  width="100px"
                />
              </ColumnGroup>

              <ColumnGroup title="回来满两周人数">
                <Column
                  title="湖北"
                  dataIndex="hbEnoughCount"
                  key="hbEnoughCount"
                  width="100px"
                />
                <Column
                  title="浙江"
                  dataIndex="zjEnoughCount"
                  key="zjEnoughCount"
                  width="100px"
                />
                <Column
                  title="河南"
                  dataIndex="hnEnoughCount"
                  key="hnEnoughCount"
                  width="100px"
                />
                <Column
                  title="安徽"
                  dataIndex="ahEnoughCount"
                  key="ahEnoughCount"
                  width="100px"
                />
                <Column
                  title="江西"
                  dataIndex="jxEnoughCount"
                  key="jxEnoughCount"
                  width="100px"
                />
                <Column
                  title="广东"
                  dataIndex="gdEnoughCount"
                  key="gdEnoughCount"
                  width="100px"
                />
                <Column
                  title="苏州"
                  dataIndex="szEnoughCount"
                  key="szEnoughCount"
                  width="100px"
                />
                <Column
                  title="江苏"
                  dataIndex="jsEnoughCount"
                  key="jsEnoughCount"
                  width="100px"
                />
                <Column
                  title="其他"
                  dataIndex="otherEnoughCount"
                  key="otherEnoughCount"
                  width="100px"
                />
              </ColumnGroup>
              <ColumnGroup title="回来未满两周人数">
                <Column
                  title="湖北"
                  dataIndex="hbNotEnoughCount"
                  key="hbNotEnoughCount"
                  width="100px"
                />
                <Column
                  title="浙江"
                  dataIndex="zjNotEnoughCount"
                  key="zjNotEnoughCount"
                  width="100px"
                />
                <Column
                  title="河南"
                  dataIndex="hnNotEnoughCount"
                  key="hnNotEnoughCount"
                  width="100px"
                />
                <Column
                  title="安徽"
                  dataIndex="ahNotEnoughCount"
                  key="ahNotEnoughCount"
                  width="100px"
                />
                <Column
                  title="江西"
                  dataIndex="jxNotEnoughCount"
                  key="jxNotEnoughCount"
                  width="100px"
                />
                <Column
                  title="广东"
                  dataIndex="gdNotEnoughCount"
                  key="gdNotEnoughCount"
                  width="100px"
                />
                <Column
                  title="苏州"
                  dataIndex="szNotEnoughCount"
                  key="firstszNotEnoughCountName"
                  width="100px"
                />
                <Column
                  title="江苏"
                  dataIndex="jsNotEnoughCount"
                  key="jsNotEnoughCount"
                  width="100px"
                />
                <Column
                  title="其他"
                  dataIndex="otherNotEnoughCount"
                  key="otherNotEnoughCount"
                  width="100px"
                />
              </ColumnGroup>
              <ColumnGroup title="未离开苏州的人数">
                <Column
                  title="湖北"
                  dataIndex="hbNotBackCount"
                  key="hbNotBackCount"
                  width="100px"
                />
                <Column
                  title="浙江"
                  dataIndex="zjNotBackCount"
                  key="zjNotBackCount"
                  width="100px"
                />
                <Column
                  title="河南"
                  dataIndex="hnNotBackCount"
                  key="hnNotBackCount"
                  width="100px"
                />
                <Column
                  title="安徽"
                  dataIndex="ahNotBackCount"
                  key="ahNotBackCount"
                  width="100px"
                />
                <Column
                  title="江西"
                  dataIndex="jxNotBackCount"
                  key="jxNotBackCount"
                  width="100px"
                />
                <Column
                  title="广东"
                  dataIndex="gdNotBackCount"
                  key="gdNotBackCount"
                  width="100px"
                />
                <Column
                  title="苏州"
                  dataIndex="szNotBackCount"
                  key="szNotBackCount"
                  width="100px"
                />
                <Column
                  title="江苏"
                  dataIndex="jsNotBackCount"
                  key="jsNotBackCount"
                  width="100px"
                />
                <Column
                  title="其他"
                  dataIndex="otherNotBackCount"
                  key="otherNotBackCount"
                  width="100px"
                />
              </ColumnGroup>
             
              {/* <Column
                title="备注"
                dataIndex="address"
                key="address"
                width="200px"
              /> */}
             
            </Table>
          </div>
        </div>
        <div className="bottom">
          版权所有：苏州中科天启遥感科技有限公司 苏ICP备13020668号-1
        </div>
      </div>
    );
  }
}

export default statisticsComp;
