import React, { Component } from "react";
import axios from "../model/axios";
import {
  Form,
  Icon,
  Input,
  Button,
  Checkbox,
  Dropdown,
  Menu,
  Table,
  Upload as Up
} from "antd";
import { BrowserRouter, Link, Route, Redirect, Switch ,withRouter  } from "react-router-dom";
import userIcon from "../image/user.png";
import uploadCss from "./upload.css";
const { Column, ColumnGroup } = Table;
export class Upload extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }
  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      let valueSub={};
      Object.keys(values).forEach(key => {
     
        if(key=="password"){
         
        }else if(values[key]==undefined){
          valueSub[key]="";
        }else{
          valueSub[key]=values[key];
        }
        valueSub['id']= localStorage.getItem("comp");
      });
      if (!err) {
        axios
        .post("/returnApi/user/addCompany/",
         valueSub
        )
        .then((response) => {
        
           alert("添加成功");
           this.props.history.push( `/ReturnWork/ListTemplate`);
          

        })
        .catch(function(error) {
          console.log(error);

        });
       // 
      }
    });
  };
  render() {
    

    const { getFieldDecorator } = this.props.form;

    return (
      <div className="ListTemplate">
        <div className="ListTemplateTitle">
          <div className="ListTemplateTitleIcon"></div>
          <div className="ListTemplateTitleFont">新增企业</div>
        </div>
        <div className="mainTable" style={{ height: "680px" }}>
          <Form onSubmit={this.handleSubmit} className="login-form" style={{"height":"600px"}}>
            <div
              style={{
                height: "50px",
                "padding-top": "150px",
                "margin-left": "20%",
                "font-size": "22px"
              }}
            >
              <div style={{ float: "left", width: "25%" }}>
                {" "}
                <span style={{ color: "red" }}>*</span>
                <span style={{ "margin-left": "5px" }}>企业名称:</span>
              </div>
              <div style={{ float: "left", "margin-left": "5%", width: "40%" }}>
                <Form.Item>
                  {getFieldDecorator("companyName", {
                    rules: [
                      {
                        required: true,

                        message: "请输入企业名称"
                      }
                    ]
                  })(
                    <div style={{ width: "100%" }}>
                      <Input
                       
                       
                        style={{ height: "38px", width: "90%" }}
                      />
                    </div>
                  )}
                </Form.Item>
              </div>
            </div>
           
            <div
              style={{
                height: "50px",
                "padding-top": "65px",
                "margin-left": "20%",
                "font-size": "22px"
              }}
            >
              <div style={{ float: "left", width: "25%" }}>
                {" "}
                统一会信用代码：
              </div>
              <div style={{ float: "left", "margin-left": "5%", width: "40%" }}> <Form.Item>
                  {getFieldDecorator("companyCode", {
                   
                  })(
                    <div style={{ width: "100%" }}>
                      <Input
                       
                      
                        style={{ height: "38px", width: "90%" }}
                      />
                    </div>
                  )}
                </Form.Item></div>
            </div>
            <div
              style={{
                height: "50px",
                "padding-top": "65px",
                "margin-left": "20%",
                "font-size": "22px"
              }}
            >
              <div style={{ float: "left", width: "25%"  }}> 经营地址：</div>
              <div style={{ float: "left", "margin-left": "5%", width: "40%" }}> <Form.Item>
                  {getFieldDecorator("address", {
                   
                  })(
                    <div style={{ width: "100%" }}>
                      <Input
                       
                      
                        style={{ height: "38px", width: "90%" }}
                      />
                    </div>
                  )}
                </Form.Item></div>
            </div>
            <div
              style={{
                height: "50px",
                "padding-top": "65px",
                "margin-left": "20%",
                "font-size": "22px"
              }}
            >
              <div style={{ float: "left", width: "25%"  }}>联系电话：</div>
              <div style={{ float: "left", "margin-left": "5%", width: "40%" }}> <Form.Item>
                  {getFieldDecorator("phoneNum", {
                   
                  })(
                    <div style={{ width: "100%" }}>
                      <Input
                       
                      
                        style={{ height: "38px", width: "90%" }}
                      />
                    </div>
                  )}
                </Form.Item></div>
            </div>
            <div style={{"clear":'both'}}></div>
            <div style={{"padding-top":'10px',"height":"80px"}}>
            <Button
            type="primary"
            htmlType="submit"
            className="login-form-button"
            style={{ width: "10%", "margin-top": "20px", height: "40px","margin-left":"35%","float":"left" }}
          >
            提交
          </Button>
          <Link to 
            ={{
              pathname: `/ReturnWork/ListTemplate`,
              
            }}
          >
          <Button
            
           
            style={{ width: "10%", "margin-top": "20px", height: "40px","margin-left":"5%" ,"float":"left","lineHeight":"40px"}}
           
          >
            取消
          </Button>
          </Link>
          </div>
          </Form>
          
        </div>
        <div className="bottom">
          版权所有：苏州中科天启遥感科技有限公司 苏ICP备13020668号-1
        </div>
      </div>
    );
  }
}
const WrappedNormalRegisterForm = Form.create({ name: "Upload" })(Upload);

export default WrappedNormalRegisterForm;
