import React, { Component } from "react";
import axios from "../model/axios";
import {
  Form,
  Icon,
  Input,
  Button,
  Checkbox,
  Dropdown,
  Menu,
  Table,
  Upload as Up
} from "antd";
import userIcon from "../image/user.png";
import uploadCss from "./upload.css";
import { BrowserRouter, Link, Route, Redirect, Switch, withRouter } from "react-router-dom";
const { Column, ColumnGroup } = Table;
export class addComp extends Component {

  constructor(props) {
    super(props);
    this.state = {
      file: {
        cnName: "",
        fgName: "",
        excelName: ""
      }
    };

  }
  onChange = (info) => {
    if (info.file.status === 'done') {
      if(info.file.response.success){
        alert("上传成功");
        this.serachData();
      }else{
        alert(info.file.response.message);
        this.forceUpdate();
      }
     
     
    } else if (info.file.status === 'error') {

      alert("上传失败");
      this.serachData();

    }
  }
  componentDidMount() {
    this.serachData();

  }
  serachData() {
    axios
      .get("/returnApi/findFileStats", {
        params: {
          id: this.props.match.params.id,
        }
      })
      .then(response => {
        this.setState((prevState, props) => ({
          file: response
        }));

      })
      .catch(function (error) {
        console.log(error);
      });
  }
  deleteExl(){
    axios
    .get("/returnApi/deleteExcelFileAndData", 
      {params:{id: this.props.match.params.id
    }})
    .then(response => {
     alert("删除成功");
     this.serachData();
    })
    .catch(function(error) {
      console.log(error);
    });
  }
  deleteWord(type){
    axios
    .get("/returnApi/deleteFileAndData", {
      params:{id: this.props.match.params.id,
      type:type
    }})
    .then(response => {
    this.serachData();
     alert("删除成功");
    
    })
    .catch(function(error) {
      console.log(error);
    });
  }
  
  getFile = index => {
    const props1 = {
      name: 'File',
      action: `/returnApi/uploadTemplates?id=${this.props.match.params.id}&Type=1`,
      headers: {
        authorization: 'authorization-text',
      },
      onChange: this.onChange,
      accept: 'application/msword,application/vnd.openxmlformats-officedocument.wordprocessingml.document',

    }
    const props2 = {
      name: 'File',
      action: `/returnApi/uploadTemplates?id=${this.props.match.params.id}&Type=0`,
      headers: {
        authorization: 'authorization-text',
      },
      onChange: this.onChange,
      accept: 'application/msword,application/vnd.openxmlformats-officedocument.wordprocessingml.document',
    }
    const props3 = {
      name: 'File',
      action: `/returnApi/uploadEmployerInfo?id=${this.props.match.params.id}`,
      headers: {
        authorization: 'authorization-text',
      },
      onChange: this.onChange,
      accept: 'application/vnd.ms-excel,application/vnd.openxmlformats-officedocument.spreadsheetml.sheet',
    }
    if (index == 0) {

      if (this.state.file.cnName != "") {

        return <div><div style={{ "float": "left", "width": "260px" }}> <a
        target="_blank"
        rel="noopener noreferrer"
        href={"/returnApi/downloadFgCn?type=1&id=" + this.props.match.params.id}
         > {this.state.file.cnName}</a></div>  <div style={{ "float": "left", "margin-left": "20px" }}> <Button onClick={() => this.deleteWord(1)}>删除</Button>
        </div>
         </div>;
      }

      return (
        <div class="uploadBorder">
          <Up {...props1}  >
            <Button>
              <Icon type="upload" /> 上传文件
              </Button>
          </Up>
        </div>
      );

    } else if (index == 1) {
      if (this.state.file.fgName != "") {
        return <div><div style={{ "float": "left", "width": "260px" }}><a
        target="_blank"
        rel="noopener noreferrer"
        href={"/returnApi/downloadFgCn?type=0&id=" + this.props.match.params.id}
      > {this.state.file.fgName}</a></div>  <div style={{ "float": "left", "margin-left": "20px" }}>
            <Button onClick={() => this.deleteWord(0)}>删除</Button>
           </div>
          </div>;
      } else {
        return (
          <div class="uploadBorder">
            <Up {...props2}>
              <Button>
                <Icon type="upload" /> 上传文件
              </Button>
            </Up>
          </div>
        );
      }
    } else {
      if (this.state.file.excelName != "") {
        return <div>

          <a
            target="_blank"
            rel="noopener noreferrer"
            href={"/returnApi/exportEmployerInfo?id=" + this.props.match.params.id}
          >
            <div style={{ "float": "left", "width": "260px" }}> {this.state.file.excelName}</div>
          </a>
          <div style={{ "float": "left", "margin-left": "20px" }}> <Button onClick={() => this.deleteExl()}>删除</Button></div>
        </div>;
      } else {
        return (
          <div class="uploadBorder">
            <Up {...props3}>
              <Button>
                <Icon type="upload" /> 上传文件
              </Button>
            </Up>
          </div>
        );
      }
    }
  };
  render() {

    return (
      <div className="ListTemplate">
        <div className="ListTemplateTitle">
          <div className="ListTemplateTitleIcon"></div>
          <div className="ListTemplateTitleFont">导入</div>
        </div>
        <div className="mainTable" style={{ height: "680px" }}>
          <div
            style={{
              height: "50px",
              "padding-top": "100px",
              "margin-left": "20%",
              "font-size": "22px"
            }}
          >
            <div style={{ float: "left", width: "30%" }}>复工复业申请书:</div>
            <div style={{ float: "left", "margin-left": "15%" }}>
              {this.getFile(0)}
            </div>
          </div>
          <div
            style={{
              height: "50px",
              "padding-top": "120px",
              "margin-left": "20%",
              "font-size": "22px"
            }}
          >
            <div style={{ float: "left", width: "30%" }}>
              企业业疫情防控承诺书:
            </div>
            <div style={{ float: "left", "margin-left": "15%" }}>
              {this.getFile(1)}
            </div>
          </div>
          <div
            style={{
              height: "50px",
              "padding-top": "120px",
              "margin-left": "20%",
              "font-size": "22px"
            }}
          >
            <div style={{ float: "left", width: "30%" }}>复工复业人员名单:</div>
            <div style={{ float: "left", "margin-left": "15%" }}>
              {this.getFile(2)}
            </div>
          </div>
          <div className="mainInput">

          </div>
          <div>
            <Link to
              ={{
                pathname: `/ReturnWork/intoComp/${this.props.match.params.id}`,

              }}>
              <Button type="primary" style={{
                width: "20%",
                "margin-top": "100px",
                height: "42px",
                "margin-left": "34%",
                float: "left",
                lineHeight: "40px"
              }}>确认</Button>
            </Link>
          </div>
        </div>

        <div className="bottom">
          版权所有：苏州中科天启遥感科技有限公司 苏ICP备13020668号-1
        </div>
      </div>
    );
  }
}

export default addComp;
