import React, { Component } from "react";
import { Form, Icon, Input, Button, Checkbox } from "antd";
import "./Login.css";
import bg from "../image/bg.png";
import ReturnWork from "./ReturnWork";
import axios from "../model/axios";
import md5 from 'js-md5'
import {
  BrowserRouter,
  Link,
  Route,
  Redirect,
  Router,
  Switch
} from "react-router-dom";

class LoginMore extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }
  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      let valueSub={};
      Object.keys(values).forEach(key => {
     
        if(key=="password"){
          valueSub[key]=md5(values[key]);
        }else if(values[key]==undefined){
          valueSub[key]="";
        }else{
          valueSub[key]=values[key];
        }
      });
      console.log(valueSub);
      if (!err) {
      
        axios
        .get("/returnApi/user/login/",{
          params:valueSub
        })
        .then((response) => {
          localStorage.setItem("comp",response.id);
          localStorage.setItem("loginName",response.loginName);
            this.props.history.push('/ReturnWork/ListTemplate');   

        })
        .catch(function(error) {
          console.log(error);

        });
       // 
      }
    });
  };
  render() {
    const { getFieldDecorator } = this.props.form;
    return (
      <div id="LoginMore">
        <Form
          onSubmit={this.handleSubmit}
          className="login-form"
          style={{ width: "80%", "margin-left": "10%", "margin-top": "40px" }}
        >
          <Form.Item>
            {getFieldDecorator("loginName", {
              rules: [{ required: true, message: "请输入账号" }]
            })(
              <Input
                prefix={
                  <Icon type="user" style={{ color: "rgba(0,0,0,.25)" }} />
                }
                placeholder="2-16位的数字、字母或邮箱"
                style={{ height: "38px" }}
              />
            )}
          </Form.Item>
          <Form.Item>
            {getFieldDecorator("password", {
              rules: [{ required: true, message: "请输入密码" }]
            })(
              <Input
                prefix={
                  <Icon type="lock" style={{ color: "rgba(0,0,0,.25)" }} />
                }
                type="password"
                placeholder="密码"
                style={{ height: "38px" }}
              />
            )}
          </Form.Item>
          <Button
            type="primary"
            htmlType="submit"
            className="login-form-button"
            style={{ width: "100%", "margin-top": "20px", height: "40px" }}
          >
            登录
          </Button>
        </Form>
      </div>
    );
  }
}
const WrappedNormalLoginForm = Form.create({ name: "normal_login" })(LoginMore);

class RegisterMore extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }
  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      let valueSub={};
      Object.keys(values).forEach(key => {
     
        if(key=="password"){
          valueSub[key]=md5(values[key]);
        }else if(values[key]==undefined){
          valueSub[key]="";
        }else{
          valueSub[key]=values[key];
        }
      });
      if (!err) {
      axios
        .post("/returnApi/user/register/",
          valueSub
        )
        .then((response) => {

           // this.props.history.push('/ReturnWork');   
           alert("注册成功");

        })
        .catch(function(error) {
          console.log(error);

        });
      }
    });
  };
  validateToNextPassword = (rule, value, callback) => {
    const { form } = this.props;

    if (value !== form.getFieldValue("password")) {
      callback("两次输入密码不一样");
    }
    callback();
  };
  render() {
    const { getFieldDecorator } = this.props.form;
    return (
      <div id="RegisterMore">
        <Form
          onSubmit={this.handleSubmit}
          className="login-form"
          style={{ width: "80%", "margin-left": "10%", "margin-top": "25px" }}
        >
          <Form.Item>
            {getFieldDecorator("loginName", {
              rules: [
                {
                  required: true,
                  min: 2,
                  max: 16,
                  message: "请输入2-16位的数字、字母或邮箱"
                }
              ]
            })(
              <div style={{ width: "100%" }}>
                <Input
                  prefix={
                    <Icon type="user" style={{ color: "rgba(0,0,0,.25)" }} />
                  }
                  placeholder="2-16位的数字、字母或邮箱"
                  style={{ height: "30px", width: "95%" }}
                />
                <span style={{ color: "red", "margin-left": "5px" }}>*</span>
              </div>
            )}
          </Form.Item>
          <Form.Item>
            {getFieldDecorator("password", {
              rules: [{ required: true, min: 6, message: "请输入密码" }]
            })(
              <div style={{ width: "100%" }}>
                <Input
                  prefix={
                    <Icon type="lock" style={{ color: "rgba(0,0,0,.25)" }} />
                  }
                  type="password"
                  placeholder="密码"
                  style={{ height: "30px", width: "95%" }}
                />
                <span style={{ color: "red", "margin-left": "5px" }}>*</span>
              </div>
            )}
          </Form.Item>
          <Form.Item>
            {getFieldDecorator("confirm", {
              rules: [
                // { required: true, min: 6, message: "请输入确认密码" },
                {
                  validator: this.validateToNextPassword
                }
              ]
            })(
              <div style={{ width: "100%" }}>
                <Input
                  prefix={
                    <Icon type="lock" style={{ color: "rgba(0,0,0,.25)" }} />
                  }
                  type="password"
                  placeholder="确认密码"
                  style={{ height: "30px", width: "95%" }}
                />
                <span style={{ color: "red", "margin-left": "5px" }}>*</span>
              </div>
            )}
          </Form.Item>
          <Form.Item>
            {getFieldDecorator("companyName", {
              rules: [{ required: true, message: "请输入企业名称" }]
            })(
              <div style={{ width: "100%" }}>
                <Input
                  prefix={
                    <Icon type="lock" style={{ color: "rgba(0,0,0,.25)" }} />
                  }
                  placeholder="企业名称"
                  style={{ height: "30px", width: "95%" }}
                />
              </div>
            )}
          </Form.Item>

          <Form.Item>
            {getFieldDecorator(
              "companyCode",
              {}
            )(
              <div style={{ width: "100%" }}>
                <Input
                  prefix={
                    <Icon type="lock" style={{ color: "rgba(0,0,0,.25)" }} />
                  }
                  placeholder="企业代码"
                  style={{ height: "30px", width: "95%" }}
                />
              </div>
            )}
          </Form.Item>
          <Form.Item>
            {getFieldDecorator(
              "phoneNum",
              {}
            )(
              <div style={{ width: "100%" }}>
                <Input
                  prefix={
                    <Icon type="lock" style={{ color: "rgba(0,0,0,.25)" }} />
                  }
                  placeholder="联系电话"
                  style={{ height: "30px", width: "95%" }}
                />
              </div>
            )}
          </Form.Item>
          <Form.Item>
            {getFieldDecorator(
              "address",
              {}
            )(
              <div style={{ width: "100%" }}>
                <Input
                  prefix={
                    <Icon type="lock" style={{ color: "rgba(0,0,0,.25)" }} />
                  }
                  placeholder="经营地址"
                  style={{ height: "30px", width: "95%" }}
                />
              </div>
            )}
          </Form.Item>
          <Button
            type="primary"
            htmlType="submit"
            className="login-form-button"
            style={{ width: "95%", "margin-top": "10px", height: "40px" }}
          >
            注册
          </Button>
        </Form>
      </div>
    );
  }
}
const WrappedNormalRegisterForm = Form.create({ name: "normal_register" })(
  RegisterMore
);

class Login extends Component {
  constructor(props) {
    super(props);
    this.state = { date: new Date(), login: true };
    //this.changeLogin = this.changeLogin.bind(this);
  }
  changeLogin = login => {
    this.setState((prevState, props) => ({
      login: login
    }));
  };
  render() {
    return (
      <div id="login_main">
        {/* <Button  type="primary">{this.state.date.toLocaleTimeString()}</Button> */}
        <div className="LoginHeader">
          <div
            className={
              this.state.login ? "LoginHeader_login_click" : "LoginHeader_login"
            }
            onClick={() => this.changeLogin(true)}
          >
            <span
              style={
                this.state.login
                  ? {
                      "margin-left": "110px",
                      "border-bottom": "3px solid #336FDF"
                    }
                  : { "margin-left": "110px" }
              }
            >
              登录
            </span>
          </div>
          <div
            className={`${
              this.state.login
                ? "LoginHeader_register"
                : "LoginHeader_register_click"
            }`}
            onClick={() => this.changeLogin(false)}
          >
            <span
              style={
                this.state.login
                  ? { "margin-left": "20px" }
                  : {
                      "margin-left": "20px",
                      "border-bottom": "3px solid #336FDF"
                    }
              }
            >
              注册
            </span>
          </div>
        </div>
        {this.state.login ? (
          <WrappedNormalLoginForm history={this.props.history} />
        ) : (
          <WrappedNormalRegisterForm history={this.props.history} />
        )}
      </div>
    );
  }
}

export default Login;
