import React, { Component } from "react";
import axios from "../model/axios";
import {
  Form,
  Icon,
  Input,
  Button,
  Checkbox,
  Dropdown,
  Menu,
  Table
} from "antd";
import userIcon from "../image/user.png";
import intoCompCss from "./intoComp.css";
import { BrowserRouter, Link, Route, Redirect, Switch ,withRouter  } from "react-router-dom";
const { Column, ColumnGroup } = Table;
export class ReturnWork extends Component {
  constructor(props) {
    super(props);
    this.state = {
      clickType: "list",
      qymc: "",
      data: [
      
      ],
      gsmc:"",
    };
  }
  changeClickType = clickType => {
    this.setState((prevState, props) => ({
      clickType: clickType
    }));
  };
  componentDidMount() {
    this.serachData();
  }
  serachData() {
    axios
      .get("/returnApi/user/getCompanyEmployeeInfo", {
        params:{id: this.props.match.params.id,
        name:this.state.qymc
        }
      })
      .then(response => {
        //console.log(response);
        this.setState((prevState, props) => ({
        data:response.data,
        gsmc:response.name,
        }));
      })
      .catch(function(error) {
        console.log(error);
      });
  }
  handleQYMC = event => {
    if (event && event.target ) {
      let value = event.target.value;
      this.setState({ qymc: value }, () => {
        //这里打印的是最新的state值
       // alert(this.state.qymc);
       this.serachData();
      });
    }
  };

  render() {
    const menu = (
      <Menu>
        <Menu.Item>
          <a
            target="_blank"
            rel="noopener noreferrer"
            href="http://www.alipay.com/"
          >
            1st menu item
          </a>
        </Menu.Item>
        <Menu.Item>
          <a
            target="_blank"
            rel="noopener noreferrer"
            href="http://www.taobao.com/"
          >
            2nd menu item
          </a>
        </Menu.Item>
        <Menu.Item>
          <a
            target="_blank"
            rel="noopener noreferrer"
            href="http://www.tmall.com/"
          >
            3rd menu item
          </a>
        </Menu.Item>
      </Menu>
    );
    const rowSelection = {
      onChange: (selectedRowKeys, selectedRows) => {
        console.log(
          `selectedRowKeys: ${selectedRowKeys}`,
          "selectedRows: ",
          selectedRows
        );
      }
    };

    return (
      <div className="ListTemplate">
        <div className="ListTemplateTitle">
          <div className="ListTemplateTitleIcon"></div>
          <div className="ListTemplateTitleFont">企业列表</div>
        </div>
        <div className="mainTable" style={{ height: "680px" }}>
          <div className="compName">{this.state.gsmc}</div>
          <div className="mainInput">
            <Input
              placeholder="请输入人员姓名"
              style={{
                "margin-left": "3%",
                width: "200px",
                "margin-top": "10px"
              }}
              onChange={event => this.handleQYMC(event)}
            />
           
              <a
            target="_blank"
            rel="noopener noreferrer"
            href={"/returnApi/exportEmployerInfo?id="+this.props.match.params.id}
          >
            <Button
              type="primary"
              style={{
                "margin-right": "3%",
                "margin-top": "10px",
                float: "right"
              }}
            >
              人员名单下载
            </Button>
            </a>
         

            {/* <Dropdown overlay={this.menu} placement="模板下载"  style={{
               "margin-right": "1%",
               "margin-top": "10px",
               float: "right"
              }}>
              <Button>模板下载<Icon type="down" /></Button>
            </Dropdown> */}
           
            <Link to 
            ={{
              pathname: `/ReturnWork/upload/${this.props.match.params.id}`,
              
            }}
          >
              <Button
                type="primary"
                style={{
                  "margin-right": "1.5%",
                  "margin-top": "10px",
                  float: "right"
                }}
              >
                导入
              </Button>
              </Link>
           
          </div>
          <div className="tableInfo">
            <Table
              dataSource={this.state.data}
              scroll={{ y: "420px", x: "2500px" }}
              rowKey="idCard"
              bordered
            >
              
              <Column
                title="姓名"
                dataIndex="name"
                key="name"
                width="120px"
                fixed
              />
              <Column
                title="身份证号"
                dataIndex="idCard"
                key="idCard"
                width="200px"
              />
              <Column
                title="性别"
                dataIndex="sex"
                key="sex"
                width="80px"
              />

              <ColumnGroup title="户籍所在地">
                <Column
                  title="省"
                  dataIndex="hjProvince"
                  key="firstName"
                  width="150px"
                />
                <Column
                  title="市"
                  dataIndex="hjCity"
                  key="hjCity"
                  width="150px"
                />
                <Column
                  title="县"
                  dataIndex="hjArea"
                  key="hjArea"
                  width="150px"
                />
              </ColumnGroup>

              <ColumnGroup title="返苏前居住地">
                <Column
                  title="省"
                  dataIndex="jzProvince"
                  key="provinc"
                  width="150px"
                />
                <Column
                  title="市"
                  dataIndex="jzCity"
                  key="city"
                  width="150px"
                />
              </ColumnGroup>
              <Column
                title="苏州详细居住地址"
                dataIndex="address"
                key="address"
                width="230px"
              />
              <Column
                title="手机号码"
                dataIndex="phoneNum"
                key="phoneNum"
                width="200px"
              />
              <Column
                title="健康状况"
                dataIndex="health"
                key="health"
                width="150px"
              />
              <Column
                title="是否和湖北接触"
                dataIndex="arriveHB"
                key="arriveHB"
                width="150px"
              />
              <Column
                title="是否离苏"
                dataIndex="leaveSZ"
                key="leaveSZ"
                width="80px"
              />
              <Column
                title="返苏时间"
                dataIndex="szBackDate"
                key="szBackDate"
                width="220px"
              />
              <Column
                title="交通工具"
                dataIndex="szBackWay"
                key="szBackWay"
                width="150px"
              />
              <Column
                title="同行人数"
                dataIndex="relativesNum"
                key="relativesNum"
                width="100px"
              />
            </Table>
          </div>
        </div>
        <div className="bottom">
          版权所有：苏州中科天启遥感科技有限公司 苏ICP备13020668号-1
        </div>
      </div>
    );
  }
}

export default ReturnWork;
