import React, { Component } from "react";

import { Form, Icon, Input, Button, Checkbox } from "antd";
import userIcon from "../image/user.png";
import ListTemplate from "./ListTemplate";
import intoComp from "./intoComp";
import returnCss from "./ReturnWork.css";
import upload from "./upload";
import addComp from "./addComp";
import updateComp from "./updateComp";
import statisticsComp from "./statisticsComp";

import { Link, Route  } from "react-router-dom";
export class ReturnWork extends Component {
  constructor(props) {
    super(props);
   
    this.state = {
      clickType: "list"
    };
  }
  changeClickType = clickType => {
    this.setState((prevState, props) => ({
      clickType: clickType
    }));
  };
  getTemplate = () => {
    if (this.state.clickType == "list") {
      return <ListTemplate history={this.props.history} />;
    } else if (this.state.clickType == "statistics") {
      return <ListTemplate history={this.props.history} />;
    }
  };
  render() {
    console.log(this.props.location);
    return (
      <div className="ReturnWork">
        <div className="top">
          <div className="topHeader">企业复工人员信息采集系统</div>
          <Link to 
            ={{
              pathname: `/ReturnWork/ListTemplate`,
            }}
          >
            <div
              className={
                this.props.location.pathname == "/ReturnWork/statisticsComp" ? "comList1" : "comList"
              }
              onClick={() => this.changeClickType("list")}
            >
              <div style={{ "margin-top": "22px" }}>企业列表</div>
            </div>
            </Link>
       
          <Link to 
            ={{
              pathname: `/ReturnWork/statisticsComp`,
              query: {clickType:"statistics"}
            }}
          >
            <div
              className={
                this.props.location.pathname == "/ReturnWork/statisticsComp" ? "comList" : "comList1"
              }
              style={{ "margin-left": "20px" }}
              onClick={() => this.changeClickType("statistics")}
            >
              <div style={{ "margin-top": "22px" }}>汇总统计</div>
            </div>
          </Link>
          <a  href={"/#/Login"}><div className="quit">退出</div></a>
            <div className="username">{localStorage.getItem("loginName")}</div>
          <div className="userIcon">
            <img src={userIcon} style={{ width: "25px" }} />
          </div>
        </div>

        <div style={{ "margin-left": "5%", width: "90%" }}>
          {/* {this.state.clickType == "list" ? (
          <ListTemplate history={this.props.history} />
        ) : (
          <ListTemplate history={this.props.history} />
        )} */}
          <Route exact path={`/ReturnWork/intoComp/:id`} component={intoComp} />
          <Route
            exact
            path={`/ReturnWork/ListTemplate`}
            component={ListTemplate}
          />
          <Route exact path={`/ReturnWork/upload/:id`} component={upload} />
          <Route exact path={`/ReturnWork/addComp`} component={addComp} />
          <Route
            exact
            path={`/ReturnWork/updateComp/:id`}
            component={updateComp}
          />
          <Route
            exact
            path={`/ReturnWork/statisticsComp`}
            component={statisticsComp}
          />
        </div>
      </div>
    );
  }
}

export default ReturnWork;
