import React from "react";
import logo from "./logo.svg";
import "./App.css";
import intoComp from "./Template/intoComp";
import {
  BrowserRouter,
  Link,
  Route,
  Redirect,
  Router,
  Switch,
  HashRouter
} from "react-router-dom";
import Login from "./Template/Login";
import ReturnWork from "./Template/ReturnWork";
function App() {
  return (
    <div style={{ height: "100%", width: "100%" }}>
      {/* <BrowserRouter>
        <Redirect path="/" to="/login" />
        <Route exact path="/login" component={Login} />      
      </BrowserRouter> */}
      <HashRouter>
        {/* <Redirect  path="/" to="/login" /> */}
        <Switch>
          <Route exact path="/Login" component={Login} />
          <Route  path="/ReturnWork" component={ReturnWork} />
         
       </Switch>
      </HashRouter>
     
    </div>
  );
}

export default App;
